.section-title {
  margin: 0;
  margin-bottom: 48px;
  font-weight: 700;
  font-size: 32px;
  line-height: 32px;

  @include on-tablet {
    margin-bottom: 56px;
    font-size: 40px;
    line-height: 40px;
  }

  @include on-desktop {
    font-size: 48px;
    line-height: 48px;
  }

  &--align--left {
    text-align: left;
    margin-bottom: 24px;
  }
}
